export default {
  name: 'FooterMenu',
  props: ['showCollectBtn', 'showExportBtn', 'collected', 'link'],
  data() {
    return {

    }
  },
  methods: {
    handleCopyLink() {
      console.log('copy', this.link)
      wx.setClipboardData({
        data: this.link,
        success: function(res) {
          wx.showToast({
            icon: 'none',
            title: '网页已复制到剪贴板'
          })
        }
      })
    },
    handleExportBtn() {
      this.$emit('exportClick')
    },
    handleCollect() {
      this.$emit('collectClick')
    },
    activeCollectIcon() {
      this.collected = true
    },
    deactiveCollectIcon() {
      this.collected = false
    }
  }
}
